html,
body {
    height: 100%;
}

body {
    margin: 0;
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#root {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.basket {
    position: fixed;
    z-index: 10;
    top: 50%;
    left: 50%;
    width: 100%;
    height: auto;
    transform: translate(-50%, -50%);
    animation: show 500ms ease;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #0000006c;
}

@keyframes show {
    from {
        top: 70%;
        opacity: 0;
    }
    to {
        top: 50%;
        opacity: 1;
    }
}

.alert {
    position: fixed;
    top: 80px;
    right: 100px;
    animation: alert 500ms ease-in-out;
}

@keyframes alert {
    from {
        top: 15%;
        opacity: 0;
    }
    to {
        top: 80px;
        opacity: 1;
    }
}
